<template>

    <div class="signing-documents-wrapper">

        <div class="order-step-info-title">
            <div class="h2">Подписание</div>
            <p>Ознакомьтесь со всеми приложенными документами, после чего вы сможете подписать их</p>
        </div>

        <div class="upload-documents">

            <div class="upload-documents">

                <template v-if="visibleDocs.length > 0">

                    <template v-for="finalDocsListItem in visibleDocs">

                        <div v-for="doc in finalDocsListItem.files"
                             :key="doc.id"
                             class="upload-documents-item">

                            <div class="upload-documents-img">
                                <div class="upload-documents-poster">{{doc.format}}</div>
                            </div>

                            <div class="upload-documents-title">
                                <a href="#"
                                   @click.prevent="openDocViewModal(doc)">{{doc.name}}</a>
                                <br>
                                <a
                                    href="#"
                                    class="download-link"
                                    @click.prevent="downloadFile(doc)">Скачать</a>
                            </div>

                            <div class="upload-documents-status">

                                <a v-if="!doc.familiarized"
                                   href="#"
                                   class="btn btn-border btn-sm"
                                   @click.prevent="openDocViewModal(doc)">Ознакомиться</a>

                                <div v-if="doc.familiarized"
                                     class="upload-success">
                                    <div class="upload-success-text">Ознакомлен</div>
                                </div>

                            </div>

                        </div>

                    </template>

                </template>

            </div>

        </div>

        <div
            v-if="!company.regbank"
            class="crediting-funds">

            <div class="h4">Условия зачисления денежных средств</div>

            <div class="crediting-funds-list">

                <div class="crediting-funds-item">

                    <div class="crediting-funds-item-left">
                        <div class="h6"
                             v-html="$t('create_order.finish_checkbox_1_title')"/>
                        <p v-html="$t('create_order.finish_checkbox_1_descr')" />
                    </div>

                    <div class="crediting-funds-item-right">
                        <div class="h6">Я согласен</div>
                        <FormGroupSwitchCheckbox v-model="openingCurrentAcc" />
                    </div>

                </div>

                <div
                    v-if="parseInt(this.order.order.purpose) === 1 || this.order.order.tarrif_id === 1"
                    class="crediting-funds-item">

                    <div class="crediting-funds-item-left">
                        <div class="h6"
                             v-html="$t('create_order.finish_checkbox_2_title')"/>
                        <p v-html="$t('create_order.finish_checkbox_2_descr')" />
                    </div>

                    <div class="crediting-funds-item-right">
                        <div class="h6">Я согласен</div>
                        <FormGroupSwitchCheckbox v-model="openingPledgeAcc" />
                    </div>

                </div>

            </div>

        </div>

        <div class="order-person-info-footer-btns">
            <button class="btn btn-secondary"
                    @click.prevent="setOrderStepMinus">Назад
            </button>
            <div class="next-step-text-info">Прочитайте и ознакомьтесь<br>со всеми документами</div>
            <button class="btn"
                    @click="openSigningModal"
                    :disabled="!canSigning">Подписать
            </button>
        </div>

        <DocView v-if="viewDocumentModal"
                 :list="viewDocument"
                 @close-modal="closeViewDocModal()"
                 @familiarized-doc="familiarizedDoc"/>

        <Popup v-if="signingModal"
               ref="signingModal"
               @close-modal="signingModal = false">

            <div class="popup-title">
                <div class="h2">Подписание заявки</div>
            </div>

            <div class="order-success">

                <div class="credit-data">
                    <div class="h4">Данные о кредите</div>
                    <ul>
                        <li></li>
                        <li>Название: Кредит «{{order.order.name}}»</li>
                        <li>Сумма: {{order.order.sum.toString().replace(/(\d)(?=(\d{3})+(\D|$))/g, '$1 ')}} рублей</li>
                        <li>Цель: {{currentTarget}}</li>
                        <li>Срок: {{order.order.term}} месяцев</li>
                        <li>Кредитная линия: {{order.order.line ? ' Да' : 'Нет'}}</li>
                        <li>Страхование жизни директора: {{order.order.insurance ? ' Да' : 'Нет'}}</li>
                        <li>Комиссия за выдачу: {{commission | numberFilter}} рублей</li>
                    </ul>
                </div>

                <div class="order-success-submit">
                    <button class="btn btn-block btn-lg"
                            :class="{'loading': load}"
                            :disabled="load"
                            @click="initDiadocScript">Подписать
                    </button>
                    <p>Подписание осуществляется при помощи ЭП</p>
                </div>

            </div>

        </Popup>

        <Popup v-if="successModal"
               ref="successModal"
               @close-modal="closeSuccessModal">

            <div class="popup-title">
                <div class="h2" v-html="$t('create_order.success_title')" />
            </div>

            <div class="popup-body">
                <div v-html="$t('create_order.success_descr')"></div>
                <a href="#"
                   class="btn btn-block btn-lg close-modal"
                   @click.prevent
                >Все понятно</a>
            </div>

        </Popup>

        <Popup v-if="errorModal"
               ref="errorModal"
               @close-modal="errorModal = false">

            <div class="popup-title">
                <div class="h2"
                     v-html="$t('create_order.error_title')" />
            </div>

            <div class="popup-body">
                <p v-html="$t('create_order.error_descr')" />
                <a href="#"
                   class="btn btn-block btn-lg close-modal">Написать в службу поддержки</a>
            </div>

        </Popup>

        <Popup v-if="diadocModal"
               ref="diadocModal"
               @close-modal="closeDiadocModal"
               classes="diadoc-plugins-modal">

            <div class="popup-title">
                <div class="h2">Для подписания документов при помощи ЭЦП Вам необходимо:</div>
            </div>

            <div class="popup-body">
                <div class="plugins-list">
                    <div class="plugins-list-item">1. Установить криптопровайдер: <br><a href="https://www.cryptopro.ru/products/cades/plugin" class="link" target="_blank">КриптоПро CSP</a></div>
                    <div class="plugins-list-item">2. Установите КриптоПро ЭЦП Browser plug-in: <br><a href="https://cryptopro.ru/products/cades/plugin/get_2_0" class="link" target="_blank">КриптоПро CSP Browser plug-in</a></div>
                </div>
            </div>

        </Popup>

        <Popup v-if="errorDiadocModal"
               :error="true"
               :classes="'error-modal'"
               :zIndex="101"
               @close-modal="errorDiadocModal = false">

            <div class="popup-title">
                <div class="h3">Ошибка</div>
            </div>

            <div class="popup-body">
                <p class="error-text">{{errorDiadocText}}</p>
                <button class="btn btn-lg btn-block close-modal">Попробовать снова</button>
            </div>

        </Popup>

        <Popup v-if="sertificatesModal"
               ref="sertificatesModal"
               @close-modal="closeSertificatesModal"
               classes="sertificates-modal">

            <div class="popup-title">
                <div class="h2">Выберите необходимый сертификат для цифровой подписи документов:</div>
            </div>

            <div class="popup-body">
                <div class="select-sertificates">
                    <div v-for="sertificate in sertificates"
                         :key="sertificate._cadesCertificate.objid"
                         class="sertificates-item"
                         :class="{'active': selectedSertificates == sertificate}"
                         @click="selectedSertificates = sertificate">
                        <span>{{sertificate.name}} (действителен до: {{parseDate(sertificate.validTo)}})</span>
                    </div>
                </div>

                <div class="sertificate-helper">
                    <p>
                        * Если не видите необходимый сертификат, то возможно он не добавлен в программе КриптоПро. Чтобы добавить сертификат в КриптоПро следуйте следующей инструкции:<br>
                        <a href="#" class="download-link" @click.prevent="instructionPopup = true">Инструкция по добавлению сертификата</a>
                    </p>
                </div>

                <button class="btn btn-block"
                        :disabled="signLoad"
                        :class="{'loading': signLoad}"
                        @click="createSignature">Подписать</button>
            </div>

        </Popup>

        <Popup v-if="mobModal"
               ref="mobModal"
               @close-modal="mobModal = false"
               classes="mob-modal">

            <div class="popup-title">
                <div class="h2">Внимание!</div>
            </div>

            <div class="popup-body">
                <p>Подписание при помощи ЭЦП невозможно с мобильного устройства. Пожалуйста, перейдите на десктопную версию и установите необходимые программы:</p>
                <div class="plugins-list">
                    <div class="plugins-list-item">1. Криптопровайдер: <br><a href="https://www.cryptopro.ru/products/cades/plugin" class="link" target="_blank">КриптоПро CSP</a></div>
                    <div class="plugins-list-item">2. КриптоПро ЭЦП Browser plug-in: <br><a href="https://cryptopro.ru/products/cades/plugin/get_2_0" class="link" target="_blank">КриптоПро CSP Browser plug-in</a></div>
                </div>
            </div>

        </Popup>

        <Popup v-if="true"
               ref="instructionPopup"
               @close-modal="instructionPopup = false"
               classes="instruction-modal">

            <div class="popup-title">
                <div class="h2">Инструкция по добавлению сертификата</div>
            </div>

            <div class="popup-body">
                <ol>
                    <li>Вставьте цифровой носитель электронной подписи</li>
                    <li>Откройте программу КриптоПро CSP на компьютере</li>
                    <li>Нажмите на вкладку "Сервис"</li>
                    <li>В окне выберите пункт "Просмотреть сертификаты в контейнере"</li>
                    <li>В открывшемся окне нажмите на кнопку "Обзор". Выберите контейнер и подтвердите свой выбор кнопкой ОК</li>
                    <li>В открывшемся окне нажмите на кнопку "Установить". После этого согласитесь с предложением заменить сертификат</li>
                    <li>Обновите страницу в браузере</li>
                </ol>
                <p>С более подробной инструкцией вы можете ознакомиться по <a href="https://cryptostore.ru/article/instruktsii/kak_ustanovit_sertifikat_podpisi_cherez_kriptopro_csp/" class="link" target="_blank">ссылке</a></p>
            </div>

        </Popup>

    </div>

</template>

<script>

    import DocView from "@/components/Blocks/DocView";
    import Popup from "@/components/Blocks/Popup";
    import FormGroupSwitchCheckbox from "@/components/Inputs/FormGroupSwitchCheckbox";
    import {mapState, mapGetters} from 'vuex'
    import moment from 'moment'

    import { getSystemInfo, getUserCertificates, createHash, createAttachedSignature } from 'crypto-pro'

    export default {
        name: "CreateOrderStep10",
        components: {FormGroupSwitchCheckbox, Popup, DocView},
        computed: {
            ...mapState({
                order: state => state.createOrder.orderFullInfo,
                visibleDocs: state => state.createOrder.visibleDocs,
                invisibleDocs: state => state.createOrder.invisibleDocs,
                company: state => state.users.company,
                tariffs: state => state.tariffs.tariffs
            }),
            ...mapGetters({
                targets: 'getTarrifsTargets'
            }),
            currentTarget() {
                const target = this.targets.filter(item => item.id === parseInt(this.order.order.purpose))[0]
                return target ? target.title : ''
            },
            canSigning() {
                let can = true
                const target = this.order.order.purpose
                const tarrif = this.order.order.tariff_id
                if ( this.visibleDocs.length > 0 ) {
                    this.visibleDocs.forEach(doc => {
                        doc.files.forEach(item => {
                            !item.familiarized ? can = false : null
                        })
                    })
                    if (!this.company.regbank) {
                        !this.openingCurrentAcc ? can = false : null
                        if (parseInt(target) === 1 || tarrif === 1) {
                            !this.openingPledgeAcc ? can = false : null
                        }
                    }
                }
                else {
                    can = false
                }
                return can
            },
            currentTariff () {
                return this.tariffs.filter(item => item.name === this.order.order.name)[0]
            },
            commission () {
                if (this.currentTariff) {
                    let res = 0
                    let sum = this.order.order.sum
                    res = sum * this.currentTariff.commissionPercent / 100
                    return res > this.currentTariff.minCommission ? res : this.currentTariff.minCommission
                } else {
                    return 0
                }
            }
        },
        data: () => ({
            viewDocumentModal: false,
            viewDocument: {},
            signingModal: false,
            successModal: false,
            errorModal: false,
            load: false,
            signLoad: false,
            openingCurrentAcc: false,
            openingPledgeAcc: false,
            diadocModal: false,
            errorDiadocText: '',
            errorDiadocModal: false,
            sertificates: [],
            sertificatesModal: false,
            selectedSertificates: {},
            mobModal: false,
            instructionPopup: false
        }),
        methods: {
            setOrderStepMinus() {
                this.$store.dispatch('prevStep')
            },
            openDocViewModal(doc) {
                this.viewDocument = doc
                this.viewDocumentModal = true
            },
            familiarizedDoc() {
                this.viewDocument.familiarized = true
            },
            closeViewDocModal() {
                this.viewDocumentModal = false
            },
            openSigningModal() {
                if ( this.canSigning ) {
                    this.signingModal = true
                }
                else {
                    alert('Прочитайте и ознакомьтесь со всеми документами')
                }
            },
            closeSuccessModal() {
                this.successModal = false
                this.$router.push('/orders')
            },
            closeDiadocModal() {
                this.diadocModal = false
            },
            closeSertificatesModal() {
                this.sertificatesModal = false
            },
            parseDate(date) {
                return moment(date).format('DD.MM.YYYY')
            },
            initDiadocScript() {
                if ( !this.load ) {
                    this.load = true
                    getSystemInfo().then(res => {
                        getUserCertificates().then(sertList => {
                            if ( sertList.length > 0 ) {
                                let sertPromises = []
                                this.sertificates = []

                                sertList.forEach(sert => {
                                    sertPromises.push(new Promise(resolve => {
                                        sert.isValid().then(() => {
                                            this.sertificates.push(sert)
                                            resolve();
                                        })
                                    }))
                                })

                                Promise.all(sertPromises).then(() => {
                                    this.load = false
                                    this.$refs.signingModal.closePopup()
                                    this.selectedSertificates = this.sertificates[0]
                                    this.sertificatesModal = true
                                })
                            }
                        }).catch(res => {
                            switch (res) {
                                case 'certificates_not_found':
                                    this.errorDiadocText = 'Не найдено ни одного сертификата.'
                                    break;
                                case 'valid_certificates_not_found':
                                    this.errorDiadocText = 'Не найдено ни одного валидного сертификата.'
                                    break;
                                case 'certificate_read_error':
                                    this.errorDiadocText = 'Ошибка чтения одного из сертификатов.'
                                    break
                            }
                            this.errorDiadocModal = true
                        })
                    }).catch(error => {
                        this.load = false
                        this.diadocModal = true
                        this.$refs.signingModal.closePopup()
                    })
                }
            },
            readFile(messageFile) {
                return new Promise(function (resolve) {
                    let fileReader = new FileReader();
                    fileReader.onload = function () {
                        resolve(this.result);
                    };
                    fileReader.readAsArrayBuffer(messageFile);
                });
            },
            generateSignature(hash) {
                const sert = this.selectedSertificates
                let signaturePromise;
                return new Promise((resolve, reject) => {signaturePromise = createAttachedSignature(sert.thumbprint, hash)
                    signaturePromise.then(function (signature) {
                        resolve(signature)
                    }, function (error) {
                        this.errorDiadocText = error.message
                        this.errorDiadocModal = true
                        this.signLoad = false
                        reject()
                    }).catch(error => {
                        this.errorDiadocText = error.message
                        this.errorDiadocModal = true
                        this.signLoad = false
                    })
                })
            },
            createSignature() {
                // eslint-disable-next-line no-useless-escape
                const name = this.selectedSertificates.name.replace(/[&\/\\#,+()$~%.'":*?<>{}]/g, '').toLowerCase().trim()
                // eslint-disable-next-line no-useless-escape
                const parsedCompanyName = this.company.name.replace(/[&\/\\#,+()$~%.'":*?<>{}]/g, '').toLowerCase().trim()
                if (name !== parsedCompanyName) {
                    this.errorDiadocText = 'Вы выбрали сертификат, который не принадлежит компании'
                    this.errorDiadocModal = true
                    return false
                }
                this.signLoad = true
                let ths = this
                let signaturePromises = []
                let sendDataVisible = []
                let sendDataInvisible = []
                this.visibleDocs.forEach((finalDocsListItem, index) => {
                    if ( finalDocsListItem.files.length !== 0 ) {
                        sendDataVisible.push({
                            id: finalDocsListItem.id,
                            docs: []
                        })
                        finalDocsListItem.files.forEach(file => {
                            signaturePromises.push(new Promise((resolve, reject) => {
                                this.$store.dispatch('getFile', file.id).then(resp => {
                                    let fileObj = new File([resp], file.name, {type: file.format})
                                    let messagePromise = this.readFile(fileObj)
                                    messagePromise.then(message => {
                                        createHash(message).then(function() {
                                            ths.generateSignature(message).then(signature => {
                                                sendDataVisible[index].docs.push({
                                                    id: file.id,
                                                    signature
                                                })
                                                resolve()
                                            }).catch(() => {
                                                this.signLoad = false
                                            })
                                        }, function(error) {
                                            this.errorDiadocText = error.message
                                            this.errorDiadocModal = true
                                            this.signLoad = false
                                            reject()
                                        })
                                    })
                                })
                            }))
                        })
                    }
                })
                this.invisibleDocs.forEach((finalDocsListItem, index) => {
                    if ( finalDocsListItem.files.length !== 0 ) {
                        sendDataInvisible.push({
                            id: finalDocsListItem.id,
                            docs: []
                        })
                        finalDocsListItem.files.forEach(file => {
                            signaturePromises.push(new Promise((resolve, reject) => {
                                this.$store.dispatch('getFile', file.id).then(resp => {
                                    let fileObj = new File([resp], file.name, {type: file.format})
                                    let messagePromise = this.readFile(fileObj)
                                    messagePromise.then(message => {
                                        createHash(message).then(function() {
                                            ths.generateSignature(message).then(signature => {
                                                sendDataInvisible[index].docs.push({
                                                    id: file.id,
                                                    signature
                                                })
                                                resolve()
                                            }).catch(() => {
                                                this.signLoad = false
                                            })
                                        }, function(error) {
                                            this.errorDiadocText = error.message
                                            this.errorDiadocModal = true
                                            this.signLoad = false
                                            reject()
                                        })
                                    })
                                })
                            }))
                        })
                    }
                })
                Promise.all(signaturePromises).then(() => {
                    let finalPromises = []
                    sendDataVisible.forEach(docs => {
                        finalPromises.push(new Promise(resolve => {
                            this.$store.dispatch('sendSignature', docs).then(res => {
                                if (res) {
                                    resolve()
                                }
                            })
                        }))
                    })
                    sendDataInvisible.forEach(docs => {
                        finalPromises.push(new Promise(resolve => {
                            this.$store.dispatch('sendSignature', docs).then(res => {
                                if (res) {
                                    resolve()
                                }
                            })
                        }))
                    })
                    Promise.all(finalPromises).then(() => {
                        this.$store.dispatch('orderResult', this.$route.params.id).then(resp => {
                            if ( resp ) {
                                this.signLoad = false
                                this.$refs.sertificatesModal.closePopup()
                                this.successModal = true
                            }
                            else {
                                this.signLoad = false
                            }
                        })
                    }).catch(() => this.signLoad = false)
                })
            },
            downloadFile(doc) {
                this.$store.dispatch('getFile', doc.id).then(blob => {
                    this.$store.dispatch('downloadFile', {content: blob, name: doc.name, format: doc.format})
                })
            }
        },
        async mounted() {
            let id = this.$route.params.id
            this.$emit('loading', true)
            this.$store.dispatch('getDocsList', id).then(() => {
                this.$emit('loading', false)
            })
            this.$store.commit('setOrderId', id)
            if (this.tariffs.length === 0) {
                await this.$store.dispatch('getTariffs')
            }
            const checkMob = () => {
                if ( window.innerWidth < 1200 ) {
                    this.mobModal = true
                }
            }
            checkMob();
            window.addEventListener('resize', checkMob)
            this.$on('hook:beforeDestroy', () => window.removeEventListener('resize', checkMob))
        }
    }
</script>
