                            <div class="business-description-td proportion" data-title="Доля в общем объеме реализации (в %)">
                                <input
                                    v-if="index === 0"
                                    type="number"
                                    max="100"
                                    class="order-person-info-change-input"
                                    :class="{invalid: !shares[0] || ( $v.mainActivities.$each[0].share.$error ? !$v.mainActivities.$each[0].share.required : false)}"
                                    v-model="item.share"
                                    v-mask="'###'"
                                    @input="item.share > 100 ? item.share = 100 : false">
                                <input
                                    v-else
                                    type="number"
                                    max="100"
