                <div class="business-description">

                    <div class="business-description-header">
                        <div class="business-description-th title">3 основных поставщика (по обороту)</div>
                        <div class="business-description-th term">Срок сотрудничества</div>
                        <div class="business-description-th proportion">Доля в общем объеме реализации (в %)</div>
                    </div>

                    <div class="business-description-body">
                        <div v-for="(item, index) in mainSuppliers"
                             :key="index"
                             class="business-description-tr">
                            <div class="business-description-td title" data-title="Поставщик">
                                <input
                                    v-if="index === 0"
                                    type="text"
                                    class="order-person-info-change-input"
                                    :class="{invalid: $v.mainSuppliers.$each[0].title.$error ? !$v.mainSuppliers.$each[0].title.required : false }"
                                    v-model="item.title">
                                <input
                                    v-else
                                    type="text"
                                    class="order-person-info-change-input"
                                    v-model="item.title">
                            </div>
                            <div class="business-description-td term" data-title="Срок сотрудничества">
                                <fg-checkbox v-model="item.term">Более 1 года</fg-checkbox>
                            </div>
                            <div class="business-description-td proportion"
                                 data-title="Доля в общем объеме реализации (в %)">
                                <input
                                    v-if="index === 0"
                                    type="number"
                                    class="order-person-info-change-input"
                                    :class="{invalid: !shares[1] || ($v.mainSuppliers.$each[0].share.$error ? !$v.mainSuppliers.$each[0].share.required : false)}"
                                    v-model="item.share"
                                    v-mask="'###'"
                                    @input="item.share > 100 ? item.share = 100 : false">
                                <input
                                    v-else
                                    type="number"
                                    class="order-person-info-change-input"
                                    v-model="item.share"
                                    v-mask="'###'"
                                    @input="item.share > 100 ? item.share = 100 : false">
                            </div>
                        </div>
                    </div>

                </div>

                <div class="business-description">

                    <div class="business-description-header">
                        <div class="business-description-th title">3 основных покупателя (по обороту)</div>
                        <div class="business-description-th term">Срок сотрудничества</div>
                        <div class="business-description-th proportion">Доля в общем объеме реализации (в %)</div>
                    </div>

                    <div class="business-description-body">
                        <div class="business-description-tr">
                            <div class="business-description-buyers-population">
<!--                                <fg-checkbox v-model="buyersPopulation.checked">Более 70% населения</fg-checkbox>-->
                                <fg-checkbox>Более 70% населения</fg-checkbox>
                            </div>
                        </div>
                        <div v-for="(item, index) in mainBuyers"
                             :key="index"
                             class="business-description-tr">
                            <div class="business-description-td title" data-title="3 основных покупателя (по обороту)">
                                <input
                                    v-if="index === 0"
                                    type="text"
                                    class="order-person-info-change-input"
                                    :class="{invalid: $v.mainBuyers.$each[0].title.$error ? !$v.mainBuyers.$each[0].title.required : false }"
                                    v-model="item.title">
                                <input
                                    v-else
                                    type="text"
                                    class="order-person-info-change-input"
                                    v-model="item.title">
                            </div>
                            <div class="business-description-td term" data-title="Срок сотрудничества">
                                <fg-checkbox v-model="item.term">Более 1 года</fg-checkbox>
                            </div>
                            <div class="business-description-td proportion" data-title="Доля в общем объеме реализации (в %)">
                                <input
                                    v-if="index === 0"
                                    type="number"
                                    class="order-person-info-change-input"
                                    :class="{invalid: !shares[2] || ( $v.mainBuyers.$each[0].share.$error ? !$v.mainBuyers.$each[0].share.required : false )}"
                                    v-model="item.share"
                                    v-mask="'###'"
                                    @input="item.share > 100 ? item.share = 100 : false">

                                <input
                                    v-else
                                    type="number"
                                    class="order-person-info-change-input"
                                    v-model="item.share"
                                    v-mask="'###'"
                                    @input="item.share > 100 ? item.share = 100 : false">
                            </div>
                        </div>
                    </div>

                </div>

            </div>

        </div>

        <div
            v-if="order.company.ul"
            class="order-step-info-block">

            <div class="order-step-info-block-title">
                <div class="h3">Структура группы компаний или холдинга, в которую входит Заемщик</div>
                <br>
            </div>

            <div class="order-step-info-block-content">

                <div class="settlement-accounts">

                    <div
                        v-for="(item, index) in holdings"
                        :key="index"
                        class="settlement-accounts-item">
                        <div class="order-person-info-list">
                            <div class="order-person-info-list-item">
                                <div class="order-person-info-list-title">Название юридического лица</div>
                                <div class="order-person-info-list-body">
                                    <div class="order-person-info-change">
                                        <input
                                            type="text"
                                            class="order-person-info-change-input"
                                            :class="{invalid: $v.holdings.$each[index].name.$error ? !$v.holdings.$each[index].name.required : false }"
                                            v-model="item.name">
                                    </div>
                                </div>
                            </div>
                            <div class="order-person-info-list-item">
                                <div class="order-person-info-list-title">ИНН</div>
                                <div class="order-person-info-list-body">
                                    <div class="order-person-info-change">
                                        <input
                                            type="text"
                                            class="order-person-info-change-input"
                                            v-mask="'############'"
                                            :class="{invalid: $v.holdings.$each[index].inn.$error ? (!$v.holdings.$each[index].inn.required || !$v.holdings.$each[index].inn.minLength) : false }"
                                            v-model="item.inn">
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="settlement-accounts-item-remove">
                            <button
                                @click="removeHoldings(index)"
                                class="btn btn-sm btn-red">Удалить</button>
                        </div>
                    </div>

                    <button
                        class="btn"
                        @click="addHoldings">Добавить юр. лицо</button>
                </div>

            </div>

        </div>

        <div class="order-person-info-footer-btns">
            <button class="btn btn-secondary"
                    @click.prevent="setOrderStepMinus">Назад</button>
            <button class="btn"
                    :class="{'loading': load}"
                    :disabled="load"
                    @click.prevent="confirmData">{{'Продолжить'}}</button>
        </div>

        <Popup v-if="errorModal"
               :error="true"
               :classes="'error-modal'"
               @close-modal="errorModal = false">

            <div class="popup-title">
                <div class="h3">Ошибка при заполнении</div>
            </div>

            <div class="popup-body">
                <p class="error-text">{{errorText}}</p>
                <button class="btn btn-lg btn-block close-modal">Хорошо</button>
            </div>

        </Popup>

    </div>

</template>

<script>

    import {required, minLength} from 'vuelidate/lib/validators'
    import Popup from "@/components/Blocks/Popup";
    import {mapState} from 'vuex'

    export default {
        name: "CreateOrderStep6",
        components: {
            Popup
        },
        validations: {
            mainActivities: {
                $each: {
                    title: {required},
                    share: {required}
                }
            },
            mainSuppliers: {
                $each: {
                    title: {required},
                    share: {required}
                }
            },
            mainBuyers: {
                $each: {
                    title: {required},
                    share: {required}
                }
            },
            holdings: {
                $each: {
                    name: {required},
                    inn: {required, minLength: minLength(12)}
                }
            }
        },
        computed: {
            ...mapState({
                order: state => state.createOrder.orderFullInfo
            }),
            mainActivities() {
                return this.order.order.share.filter(item => item.type === 1)
            },
            mainSuppliers() {
                return this.order.order.share.filter(item => item.type === 2)
            },
            mainBuyers() {
                return this.order.order.share.filter(item => item.type === 3)
            },
            buyersPopulation() {
                return this.order.order.share.filter(item => item.type === 4)[0]
            },
            holdings () {
                return this.order.order.holdings
            }
        },
        data: () => ({
            errorModal: false,
            load: false,
            shares: [
                true,
                true,
                true
            ],
            errorText: 'Введите обязательные поля'
        }),
        methods: {
            addHoldings() {
                this.holdings.push({
                    name: '',
                    inn: ''
                })
            },
            removeHoldings(index) {
                this.holdings.splice(index, 1)
            },
            confirmData() {
                this.shares[0] = this.mainActivities.reduce((prev, curr) => prev + (curr.share ? parseFloat(curr.share) : 0), 0)
                this.shares[1] = this.mainSuppliers.reduce((prev, curr) => prev + (curr.share ? parseFloat(curr.share) : 0), 0)
                this.shares[2] = this.mainBuyers.reduce((prev, curr) => prev + (curr.share ? parseFloat(curr.share) : 0), 0)

                if (
                    this.$v.mainActivities.$each[0].$invalid ||
                    this.$v.mainSuppliers.$each[0].$invalid ||
                    this.$v.mainBuyers.$each[0].$invalid
                ) {
                    this.$v.$touch();
                    this.errorText = 'Введите обязательные поля'
                    this.errorModal = true
                    return
                }
                if (this.shares[0] > 100) {
                    this.errorText = 'Сумма долей в общем объеме реализации в основных видах деятельности не может превышать 100%'
                    this.errorModal = true
                    return
                }
                if (this.shares[1] > 100) {
                    this.errorText = 'Сумма долей в общем объеме реализации у основных поставщиков (по обороту) не может превышать 100%'
                    this.errorModal = true
                    return
                }
                if (this.shares[2] > 100) {
                    this.errorText = 'Сумма долей в общем объеме реализации у основных покупателей (по обороту) не может превышать 100%'
                    this.errorModal = true
                    return
                }
                if (this.order.company.ul) {
                    if (this.$v.holdings.$invalid) {
                        this.$v.holdings.$touch()
                        this.errorText = 'Проверьте корректность введеных данных'
                        this.errorModal = true
                        return
                    }
                }
                this.setOrderStepPlus()
            },
            setOrderStepPlus() {
                this.load = true
                let fields = {share: this.order.order.share}
                if (this.order.company.ul && this.holdings.length > 0) {
                    fields.holdings = this.holdings
                }
                this.$store.dispatch('updateOrderOnlyOne', {id: this.$route.params.id, fields}).then(() => {
                    this.$store.dispatch('nextStep')
                    this.load = false
                })
            },
            setOrderStepMinus() {
                this.$store.dispatch('prevStep')
            }
        }
    }
</script>
